.warp-main {
  width: 100%;
  min-height: 100vh;
  height: auto;
  background-color: #F5F9FC; }
  .warp-main .main {
    position: fixed;
    height: 100vh;
    width: 100%;
    margin-block-start: 0; }
    .warp-main .main .tab-main {
      height: 100vh;
      width: 20%;
      background-color: aqua;
      position: fixed;
      left: 0;
      margin-block-start: 0; }
    .warp-main .main .card-content {
      position: fixed;
      margin-block-start: 0;
      right: 0;
      width: 80%; }

.warp-table {
  margin: 5rem;
  display: flex;
  flex-direction: column; }
  @media (max-width: 575.98px) {
    .warp-table {
      display: none; } }
  .warp-table .table .react-bs-table-container .react-bs-table {
    background-color: white; }
  .warp-table .table .react-bs-table-container .react-bs-table-bordered {
    border-radius: 0.2rem 0.2rem 0 0; }
    .warp-table .table .react-bs-table-container .react-bs-table-bordered .react-bs-container-header {
      background-color: #cccccc;
      border-radius: 0.2rem 0.2rem 0 0; }
      .warp-table .table .react-bs-table-container .react-bs-table-bordered .react-bs-container-header table thead tr th {
        text-align: center !important; }
